.App {
  position: absolute;
  max-height: 2500px;
  min-width: 800px;
  width: 1366px;
  margin: 5px;
  background-image: linear-gradient( 150deg, #e7b4de 30%, #86a1e2 70%, #a0cffa 100%);
  text-align: center;
  border: 1px solid #f97200;
  box-sizing: border-box;
  border-radius: 25px;
  color: #000;
  height: auto;
}
.App .pages {
  position: absolute;
  top:130px;
  left:250px;
  background-color: white;
  border: 1px solid rgb(172, 170, 168);
  box-sizing: border-box;
  border-radius: 25px;
  box-shadow: 2px 5px gray;
  max-width: 900px;
  max-height: 2000px;
  height: auto;
}
.App .leftSide {
  position: absolute;
  top:130px;
  left:10px;
  background-color: white;
  border: 1px solid rgb(172, 170, 168);
  box-sizing: border-box;
  border-radius: 25px;
  box-shadow: 0.2em 3px gray;
  min-width: 230px;
  min-height: 450px;
}

